import axios from "@/axios.js"

export default {
  fetchOrders({ commit }, batchId) {
    return new Promise((resolve, reject) => {
      axios.get(`/admin/batches/${batchId}/orders`)
        .then((response) => {
          commit('SET_ORDERS', this.state.responseDataPath ? response[this.state.responseDataPath].data : response.data)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
  fetchOrder({ commit }, orderId) {
    return new Promise((resolve, reject) => {
      axios.get(`/admin/orders/${orderId}`)
        .then((response) => {
          commit('SET_ORDER', this.state.responseDataPath ? response[this.state.responseDataPath].data : response.data)
          resolve(this.state.responseDataPath ? response[this.state.responseDataPath] : response)
        })
        .catch((error) => { reject(error) })
    })
  },
  addOrder(context, data) {
    return new Promise((resolve, reject) => {
      axios.post("/admin/orders", {data: data})
        .then((response) => {
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
  updateOrder({ commit }, data) {
    return new Promise((resolve, reject) => {
      axios.post(`/admin/orders/${data.id}`, {data: data})
        .then((response) => {
          commit('UPDATE_ORDER', Object.assign(data, {id: response.data.id}))
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
  removeOrder({ commit }, orderId) {
    return new Promise((resolve, reject) => {
      axios.delete(`/admin/orders/${orderId}`)
        .then((response) => {
          commit('REMOVE_ORDER', orderId)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
  fetchOrdersReport({ commit }, batchId) {
    return new Promise((resolve, reject) => {
      axios.get(`/admin/orders/${batchId}/orders-report`)
        .then((response) => {
          commit('SET_ORDERS_REPORT', this.state.responseDataPath ? response[this.state.responseDataPath].data : response.data)
          resolve(response)
        })
        .catch((error) => { reject(error) })
    })
  },
}
