<template>
  <div id="user-edit-tab-info">

    <!-- Content Row -->
    <div class="vx-row">
      <div class="vx-col md:w-1/2 w-full">
        <div class="w-full mt-4">
          <label class="vs-input--label">Year</label>
          <datepicker v-model="data_local.year" :minimumView="'year'" :maximumView="'year'" v-validate="'required'" name="year" :language="languages[language]" format="yyyy"></datepicker>
          <span class="text-danger text-sm"  v-show="errors.has('year')">{{ errors.first('year') }}</span>
        </div>

        <div class="w-full mt-4">
          <label class="vs-input--label">Date Start</label>
          <datepicker v-model="data_local.date_start" v-validate="'required'" name="date_start" :language="languages[language]" format="dd/MM/yyyy"></datepicker>
        </div>
        <span class="text-danger text-sm"  v-show="errors.has('date_start')">{{ errors.first('date_start') }}</span>

        <div class="w-full mt-4">
          <label class="vs-input--label">Date End</label>
          <datepicker v-model="data_local.date_end" v-validate="'required'" name="date_end" :language="languages[language]" format="dd/MM/yyyy"></datepicker>
        </div>
        <span class="text-danger text-sm"  v-show="errors.has('date_end')">{{ errors.first('date_end') }}</span>
      </div>

      <div class="vx-col md:w-1/2 w-full">
        <div class="w-full mt-4">
          <label class="vs-input--label">Date Payment</label>
          <datepicker v-model="data_local.date_payment" v-validate="'required'" name="date_payment" :language="languages[language]" format="dd/MM/yyyy"></datepicker>
        </div>
        <span class="text-danger text-sm"  v-show="errors.has('date_payment')">{{ errors.first('date_payment') }}</span>

        <div class="w-full mt-4">
          <label class="vs-input--label">Date Delivery</label>
          <datepicker v-model="data_local.date_delivery" v-validate="'required'" name="date_delivery" :language="languages[language]" format="dd/MM/yyyy"></datepicker>
        </div>
        <span class="text-danger text-sm"  v-show="errors.has('date_delivery')">{{ errors.first('date_delivery') }}</span>
      </div>
    </div>

    <!-- Save & Reset Button -->
    <div class="vx-row">
      <div class="vx-col w-full">
        <div class="mt-8 flex flex-wrap items-center justify-end">
          <vs-button class="ml-auto mt-2" @click="save_changes" :disabled="!validateForm">Save Changes</vs-button>
          <vs-button class="ml-4 mt-2" type="border" color="warning" @click="reset_data">Reset</vs-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import vSelect from 'vue-select'
import Datepicker from 'vuejs-datepicker';
import * as lang from 'vuejs-datepicker/src/locale';

export default {
  components: {
    vSelect,
    Datepicker
  },
  props: {
    data: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      language: "pt-BR",
      languages: lang,
      data_local: JSON.parse(JSON.stringify(this.data)),
    }
  },
  computed: {
    validateForm() {
      return !this.errors.any()
    }
  },
  methods: {
    save_changes() {
      if(!this.validateForm) return

      this.$store.dispatch("orderManagement/updateOrder", this.data_local)
      .then(res => { this.order_data = res.data.attributes })
      .catch(err => {
        if(err.response.status === 404) {
          this.order_not_found = true
          return
        }
        console.error(err) })

      // Here will go your API call for updating data
      // You can get data in "this.data_local"

    },
    reset_data() {
      this.data_local = JSON.parse(JSON.stringify(this.data))
    }
  },
}
</script>
