<template>
  <div id="page-order-edit">

    <vs-alert color="danger" title="Order Not Found" :active.sync="order_not_found">
      <span>Order record with id: {{ $route.params.orderId }} not found. </span>
      <span>
        <span>Check </span><router-link :to="{name:'page-order-list'}" class="text-inherit underline">All Orders</router-link>
      </span>
    </vs-alert>

    <vx-card v-if="order_data">

      <div slot="no-body" class="tabs-container px-6 pt-6">

        <vs-tabs v-model="activeTab" class="tab-action-btn-fill-conatiner">
          <vs-tab label="Order" icon-pack="feather" icon="icon-order">
            <div class="tab-text">
              <order-edit-tab-account class="mt-4" :data="order_data" />
            </div>
          </vs-tab>
        </vs-tabs>

      </div>

    </vx-card>
  </div>
</template>

<script>
import OrderEditTabAccount     from "./OrderEditTabAccount.vue"

// Store Module
import moduleOrderManagement from '@/store/order-management/moduleOrderManagement.js'

export default {
  components: {
    OrderEditTabAccount
  },
  data() {
    return {
      order_data: null,
      order_not_found: false,

      /*
        This property is created for fetching latest data from API when tab is changed

        Please check it's watcher
      */
      activeTab: 0,
    }
  },
  watch: {
    activeTab() {
      this.fetch_order_data(this.$route.params.orderId)
    }
  },
  methods: {
    fetch_order_data(orderId) {
      this.$store.dispatch("orderManagement/fetchOrder", orderId)
        .then(res => { this.order_data = res.data.attributes })
        .catch(err => {
          if(err.response.status === 404) {
            this.order_not_found = true
            return
          }
          console.error(err) })
    }
  },
  created() {
    // Register Module OrderManagement Module
    if(!moduleOrderManagement.isRegistered) {
      this.$store.registerModule('orderManagement', moduleOrderManagement)
      moduleOrderManagement.isRegistered = true
    }
    this.fetch_order_data(this.$route.params.orderId)
  }
}

</script>
